import React, { Component } from 'react';
import FaqItem from "./FaqItem";


import questions from "../../assets/data/faq";

class Faq extends Component {

  render() {

    const faqItems = questions.map( (question, key) => {
      return (
        <FaqItem key={key} question={question.question}>
            <p>{question.answer}</p>
        </FaqItem>
      )
    });

    return (
        <div>
            {faqItems}
        </div>
    )
  }


}


export default Faq;



