import React, { Component } from 'react';

class TimeLine extends Component {


  render() {
    return (
      <ul className="text-left timeline">
        <li>
         <div className="timeline-badge success"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">September 28th 2018 - Airdrop Challenge kickoff</h4>
           </div>
           <div className="timeline-body">
             <p>The community can fullfill challenges and get rewarded in CryptoPepes and PEP tokens</p>

             <p>If you are one of the top participants there are awesome prices to be won.</p>
           </div>
         </div>
        </li>
        <li>
         <div className="timeline-badge"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">Early October 2018 - POW mining period</h4>
           </div>
           <div className="timeline-body">
             <p>The CryptoPepes games goes live on Ethereum mainnet and mining kicks off.</p>

             <p>During the 3 month mining period miners are rewarded ERC721 CryptoPepes and PEP tokens which will be the native currency of the PepChain.</p>
           </div>
         </div>
        </li>
        <li>
         <div className="timeline-badge"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">Q4 2018 - PepChain testnet launch</h4>
           </div>
           <div className="timeline-body">
             <p>The PepChain testnet gets created and we welcome the community to test out our Ethereum compatible DPOS sidechain.</p>
             <p>Community members can participate in the DPOS voting and become a validator themselves.</p>
           </div>
         </div>
        </li>
        <li>
         <div className="timeline-badge"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">Q1 2019 - PepChain mainnet launch</h4>
           </div>
           <div className="timeline-body">
             <p>The PepChain goes live and during a small POA period users select the first validators of the PepChain.</p>
             <p>You can now start earning PEP by voting on validators and sharing in their block rewards.</p>
             <p>CryptoPepes will also be launched at this time.</p>
           </div>
         </div>
        </li>

        <li>
         <div className="timeline-badge"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">Q2 2019 - More DApps deployed on the PepChain</h4>
           </div>
           <div className="timeline-body">
             <p>More DApps are released on the PepChain.</p>
           </div>
         </div>
        </li>

        <li>
         <div className="timeline-badge"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">Q3 2019 - Bridging with Ethereum</h4>
           </div>
           <div className="timeline-body">
             <p>Users are now able to transfer PEP and Ether between the PepChain and Ethereum mainnet.</p>
           </div>
         </div>
        </li>

        <li>
         <div className="timeline-badge"></div>
         <div className="timeline-panel">
           <div className="timeline-heading">
             <h4 className="timeline-title">Q4 2019 - PepChain sidechains</h4>
           </div>
           <div className="timeline-body">
             <p>To scale the PepChain developers can create sidechains of the PepChain which are secured on the PepChain.</p>
           </div>
         </div>
        </li>
      </ul>
    )
  }


}

export default TimeLine;



