import React, { Component } from 'react';
import {Container, Row, Col} from 'reactstrap';
import Icon, { Stack } from '@mdi/react'
import { mdiTwitter, mdiTelegram, mdiReddit, mdiMedium } from '@mdi/js'

class Footer extends Component {
  render(){
    return(
      <div className="footer padded-content">
          <Container className="text-center">
            <a target="_blank" href="https://twitter.com/pepchain"><Icon path={mdiTwitter} size={2.5}/></a>
            <a target="_blank" href="https://t.me/PepChain"><Icon path={mdiTelegram} size={2.5}/></a>
            <a target="_blank" href="https://www.reddit.com/r/pepchain/"><Icon path={mdiReddit} size={2.5}/></a>
            <a target="_blank" href="https://medium.com/@pepchain"><Icon path={mdiMedium} size={2.5}/></a>
          </Container>
      </div>
    )
  }
}

export default Footer;



