import React from "react";


export default [
  { question: "What is PEP?",
    answer: (<span>
      <p>PEP is the native Currency of the PepChain. You can use it to pay transaction fees, transfer value or vote on validators creating the blocks on the PepChain. </p>
    </span>)
  },
  { question: "How is PEP distributed?",
    answer: (<span>
      <p>40 million of the 50 million total initial supply will be mined during the mining period in which miners will be rewarded 2500 PEP per blockreward. </p>
      <p>The mining will take place on Ethereum mainnet with the same protocol as 0xbitcoin uses and is mined together with the ERC721 tokens of the CryptoPepes game our first dApp.</p>
    </span>)
  },
  { question: "How does PepChain compare to networks like POA.network and GoChain",
    answer: (<span>
      <p>PepChain uses Delegated Proof of Stake as the consensus model allowing any user to benefit and govern the network based on their stake in the project. </p>
      <p>GoChain and POA.network use Proof of authority as their consensus model where only the validators themselves can add or remove validators.</p>
      <p>We believe DPOS is superior to POA as it is more decentralised and benefits all PEP holders instead of just a few big companies while still delivering the same performance advantages.</p>
    </span>)
  },
  {
    question: "How are you funding the project?",
    answer: (<span>
      <p>We are not holding an ICO and are funding the initial development ourselves.</p>
      <p>5% of the initial supply will be send to a multi signature account controlled by the development team at the start of the mining period. Another 5% will be released during this period. Another 10% will become available in the first year of the DPOS fase.</p>
      <p>These funds are used to fund development, marketing and airdops. Additionally we will be developing dApps on top of the PepChain that will generate revenue to be used to further develop the PepChain.</p>
    </span>)
  },
  {
    question: "What is the connection to CryptoPepes.io?",
    answer: (<span>
      <p>PepChain was founded by the same team as CryptoPepes.</p>
      <p>We realised there was a demand for a fast, low cost, decentralised and open-source network that anyone can use.</p>
      <p>Revenue generated will be used to both develop CryptoPepes and the PepChain project.</p>
    </span>)
  },
  {
    question: "How do I connect to the PepChain?",
    answer: (<span>
      <p>The PepChain is not live yet but we will update this answer and notify you about the progress via our social media channels.</p>
    </span>)
  },
  {
    question: "My question is not in here.",
    answer: (<span>
      <p>Feel free to contact us via one of the social media channels displayed below.</p>
    </span>)
  }


]



